<template>
  <div id="analyseDetecDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav">
              <my-location title="基因检出率分析"></my-location>
              <my-navigat activ="2-1"></my-navigat>
          </div>
          <div class="ho-cotet">
              <div class="inputG">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="70px" :inline="true" class="demo-form-inline">
                      <el-form-item label="基因" prop="gen">
                          <el-input v-model="formSamInfo.gen" style="width: 460px;" @keyup.enter.native="addGene"></el-input>
                          <el-button icon="plus" @click="addGene"></el-button>
                      </el-form-item>
                      <el-form-item label="类型统计" prop="typeAnalyse">
                          <el-select  v-model="formSamInfo.typeAnalyse" placeholder="选择类型统计">
                              <el-option label="按动物统计" value="1"></el-option>
                              <el-option label="按采样地统计" value="2"></el-option>
                              <el-option label="按来源统计" value="3"></el-option>
                              <el-option label="按省份统计" value="4"></el-option>
                              <el-option label="按动物大类统计" value="5"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="区间" prop="remark">
                          <el-input v-model="formSamInfo.remark" style="width: 510px;"></el-input>
                      </el-form-item>
                      <el-form-item label="时间方式" prop="timeStyle">
                          <el-select  v-model="formSamInfo.timeStyle" placeholder="选择时间方式">
                              <el-option label="按年统计" value="1"></el-option>
                              <el-option label="按半年统计" value="3"></el-option>
                              <el-option label="按季度统计" value="4"></el-option>
                              <el-option label="按月统计" value="2"></el-option>
                          </el-select>
                      </el-form-item>
                  </el-form>
              </div>
              <div style="font-size: 12px; font-weight: normal; margin-left:70px; color: #F44336;">
                ↑ 此处“区间”选项为选填。若填，下面生成报表通向区间分析；若不填，下面生成报表通向检出分析。 ↑
              </div>
              <div id="gen">
                <span>基因：</span><span v-if="genArr.length<=0">( 未输入 )</span> <span v-for="(item, index) in genArr">{{ item }}{{ index < genArr.length-1 ? ' 、' : null }}</span>
              </div>

              <div class="youVeSelect" style="padding-top: 20px;border-top: 1px dashed #ccc;">
                  <h4><b>菌株编号：</b> <el-tag type="primary" v-if="selectInfo.strainno!=''"> {{ selectInfo.strainno }} </el-tag> </h4>
                  <h4><b>内部编号：</b> <el-tag type="primary" v-if="selectInfo.inno!=''"> {{ selectInfo.inno }} </el-tag> </h4>
                  <h4><b>样品编号：</b> <el-tag type="primary" v-if="selectInfo.sampleno!=''"> {{ selectInfo.sampleno }} </el-tag> </h4>
                  <h4><b>基因名称：</b> <el-tag type="primary" v-if="selectInfo.genename!=''"> {{ selectInfo.genename }} </el-tag> </h4>
                  <h4><b>省份：</b> <el-tag type="primary" v-for="province in selectInfo.province">{{ province }} </el-tag> </h4>
                  <h4><b>动物：</b> <el-tag type="primary" v-for="animal in selectInfo.animal">{{ animal }} </el-tag> </h4>
                  <h4><b>一级亚型：</b> <el-tag type="primary" v-if="selectInfo.subType1">{{ selectInfo.subType1 }} </el-tag> </h4>
                  <h4><b>二级亚型：</b> <el-tag type="primary" v-if="selectInfo.subType2">{{ selectInfo.subType2 }} </el-tag> </h4>
                  <h4><b>三级亚型：</b> <el-tag type="primary" v-for="subitem in selectInfo.subType3">{{ subitem }} </el-tag> </h4>
                  <h4><b>开始时间：</b> <el-tag type="primary" v-if="selectInfo.date1!=''"> {{ selectInfo.date1 }} </el-tag> </h4>
                  <h4><b>结束时间：</b> <el-tag type="primary" v-if="selectInfo.date2!=''"> {{ selectInfo.date2 }} </el-tag> </h4>
                  <h4><b>采样地：</b> <el-tag type="primary" v-if="selectInfo.caiyangdi"> {{ selectInfo.caiyangdi }} </el-tag> </h4>
                  <h4><b>来源：</b> <el-tag type="primary" v-for="source in selectInfo.source">{{ source }} </el-tag> </h4>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button icon="d-arrow-left" @click="toDetectionRate">返回检出率分析</el-button>
                    <el-button type="primary" icon="date" @click="makeChart" :loading="loadingBtn">生成报表</el-button>
                  </el-button-group>
              </div>
          </div>
          <div class="clearfix"></div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import axios from 'axios'
import dataRules from './../publicAPI/dataRules'
import config from './../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data() {
      return {
          URL: config.rootURL,
          selectInfo: {
            inno: "",
            sampleno: "",
            strainno: "",
            province: [],
            caiyangdi: "",
            genename: "",
            animal: [],
            source: [],
            date1: "",
            date2: "",
            subType1: "",
            subType2: "",
            subType3: [],
            remark: "",
          },
          formSamInfo: {
              gen: "",
              typeAnalyse: '',
              timeStyle: '',
              remark: '',
          },
          genArr: [],
          loadingBtn: false,
      };
  },
  methods:{
      toDetectionRate(){
          this.$router.push({path: '/geneJianchuRate'})
      },
      addGene(){
          let that = this;
          this.genArr.push(that.formSamInfo.gen);
          this.formSamInfo.gen = "";
      },
      makeChart(){
          let that = this;
          that.loadingBtn = true
          var typeAnalyse = "";
          if( this.formSamInfo.typeAnalyse == '1' ){
              typeAnalyse = '动物';
          }else if( this.formSamInfo.typeAnalyse == '2' ){
              typeAnalyse = '采样地';
          }else if( this.formSamInfo.typeAnalyse == '3' ){
              typeAnalyse = '来源';
          }else if( this.formSamInfo.typeAnalyse == '4' ){
              typeAnalyse = '省份';
              if(this.formSamInfo.timeStyle=="1"){
                localStorage.setItem('ResisYearType', "年");
              }else{
                localStorage.setItem('ResisYearType', "非年");
              }
          }else if( this.formSamInfo.typeAnalyse == '5' ){
              typeAnalyse = '动物大类';
          }
          localStorage.setItem('geneType', typeAnalyse ); //按什么统计

          let genStr = "";
          for( let i = 0; i < that.genArr.length; i ++ ){
            genStr += that.genArr[i] + ",";
          }
          localStorage.setItem('genAnalysArr', JSON.stringify(that.genArr) );
          localStorage.setItem('genAnalysStr', genStr );

          var makeChartObj = {
              inno: this.selectInfo.inno,
              animalname: this.selectInfo.animal,
              beginDate: this.selectInfo.date1,
              endDate: this.selectInfo.date2,
              farmName: this.selectInfo.caiyangdi,
              gen: genStr,
              genalias: this.selectInfo.inno,
              genname: this.selectInfo.genename,
              genotyping1: this.selectInfo.subType1.split(""),
              genotyping2: this.selectInfo.subType2.split(""),
              genotyping3: this.selectInfo.subType3,
              genremarks: this.selectInfo.remark,
              sampleProvince: this.selectInfo.province,
              sampleno: this.selectInfo.sampleno,
              statisticsType: this.formSamInfo.typeAnalyse,
              strainno: this.selectInfo.strainno,
              timeWay: this.formSamInfo.timeStyle,
          }

          if(this.formSamInfo.remark === ""){ //没有填区间
            localStorage.setItem('geneWhichChart','耐药基因检出率分析');
            axios.post(that.URL + '/genanalysis/analysis', makeChartObj)
              .then((res) => {
                  if(res.data.state){
                      localStorage.setItem('geneChart',JSON.stringify(res.data.value));
                      that.loadingBtn = false
                      this.$router.push({ path: '/analysGeneResis/analyseGene' })
                  }else{
                      this.$message({
                        showClose: true,
                        message: res.data.value,
                        type: 'error'
                      });
                  }
              })
              .catch((error) => {
                  console.error(error);
              })
          } else { //填区间
            makeChartObj.interval = that.formSamInfo.remark;
            localStorage.setItem('geneWhichChart','区间分布分析');
            axios.post(that.URL + '/genanalysis/interval/analysis', makeChartObj)
              .then((res) => {
                  if(res.data.state){
                      localStorage.setItem('geneChart',JSON.stringify(res.data.value));
                      that.loadingBtn = false
                      this.$router.push({ path: '/analysGeneResis/analyseGene' })
                  }else{
                      this.$message({
                        showClose: true,
                        message: res.data.value,
                        type: 'error'
                      });
                  }
              })
              .catch((error) => {
                  console.error(error);
              })
          }


      }
  },
  created(){
      let that = this

      var obj = JSON.parse(localStorage.getItem('geneJianchuRate'))
      console.log(localStorage.getItem('geneJianchuRate'))
      this.selectInfo.inno = obj.inno
      this.selectInfo.sampleno = obj.sampleno
      this.selectInfo.strainno = obj.strainno
      this.selectInfo.province = obj.province
      this.selectInfo.caiyangdi = obj.caiyangdi
      this.selectInfo.genename = obj.genename
      this.selectInfo.animal = obj.animal
      this.selectInfo.source = obj.source
      this.selectInfo.date1 = obj.date1
      this.selectInfo.date2 = obj.date2
      this.selectInfo.subType1 = obj.subType1
      this.selectInfo.subType2 = obj.subType2
      this.selectInfo.subType3 = obj.subType3
      this.selectInfo.remark = obj.remark
  },
  destroyed(){
    //   localStorage.removeItem('detectionRate')
  }
}

</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
.youVeSelect{
    padding-left: 80px;
}
.youVeSelect h4{
    font-weight: normal;
    color: #475669;
    font-size: 14px;
    line-height: 34px;
}
.youVeSelect b{
    color: #1D8CE0;
    font-weight: normal;
    display: inline-block;
    width: 90px;
}
.operatBtnG{
    margin-top: 26px;
    padding-left: 70px;
}
.el-tag{
    margin-right: 10px;
    text-align: center;
    font-size: 14px;
}
#gen{
    margin: 28px 26px 10px;
}
#gen span{
    color: #5e6d82;
    font-size: 14px;
    font-weight: normal;
    margin-left:5px;
}
</style>



