<template>
  <div id="jianchuDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav">
              <my-location title="耐药性分析-全部"></my-location>
              <my-navigat activ="2-2"></my-navigat>
          </div>
          <div class="ho-cotet">

              <div class="operatBtnG">
                  <el-button-group>
                    <el-button icon="d-arrow-left" @click="toInterface" style="border-radius: 4px">返回分析界面</el-button>
                    <!-- <el-button type="primary" icon="d-caret">导出表格</el-button> -->
                  </el-button-group>
                  <el-select v-model="color" multiple placeholder="请选择颜色" v-if="showChoic == 2" style="float:right;margin-right:30px;width: 480px;">
                      <el-option :value="item" v-for="item in colorSelect">
                          <span :style="{background: item}" class="colorSpan">  </span>
                          <span style="float: left; margin-left:30px;">{{ item }}</span>
                      </el-option>
                  </el-select>
              </div>
              <div class="showChoicDiv" v-if="typeAnalyse === '省份' && resisYearType === '年'">
                  <el-radio class="radio" v-model="showChoic" label="1">显示地图</el-radio>
                  <el-radio class="radio" v-model="showChoic" label="2">显示表格</el-radio>
              </div>
              <div class="mapDIV" v-show="showChoic == 1">
                  <div class="sep10"></div>
                  <el-select v-model="year" placeholder="请选择年份">
                      <el-option value="2011"></el-option>
                      <el-option value="2012"></el-option>
                      <el-option value="2013"></el-option>
                      <el-option value="2014"></el-option>
                      <el-option value="2015"></el-option>
                      <el-option value="2016"></el-option>
                      <el-option value="2017"></el-option>
                  </el-select>
                  <div class="sep10"></div>
                  <div id="map" style="width: 1090px;height:600px;"></div>
              </div>
              <div class="chartDIV" v-show="showChoic == 2">
                  <div :id="'chart'+(item-1)" style="width: 1090px;height:450px;" v-for="item in chartLen" class="chartCanvas"></div>
              </div>

          </div>
          <div class="clearfix"></div>
      </div>
  </div>
</template>

<script>
import myHeader from './../../components/header'
import myLocation from './../../components/location'
import myNavigat from './../../components/navigat'
import axios from 'axios'
import dataRules from './../../publicAPI/dataRules'
import config from './../../publicAPI/config'
import echarts from 'echarts'
import chinaMap from './../../publicAPI/china.json'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data() {
      return {
          URL: config.rootURL,
          showChoic: '2',
          color: [],
          colorSelect: ['#7cb5ec', '#f7a35c', '#8085e9', '#f15c80', '#e4d354', '#2b908f', '#f45b5b', '#91e8e1', '#434348'],
          colorChart:  ['#7cb5ec', '#f7a35c', '#8085e9', '#f15c80', '#e4d354', '#2b908f', '#f45b5b', '#91e8e1', '#434348'],
          chartSet: JSON.parse(localStorage.getItem('ResisChart')),
          chartLen: JSON.parse(localStorage.getItem('ResisChart')).length,
          typeAnalyse: localStorage.getItem('ResisType'),
          strainAnalyse: localStorage.getItem('ResisStrain'),
          drugAnalyse: localStorage.getItem('ResisDrug'),
          drugAnalyseArr: JSON.parse(localStorage.getItem('ResisDrugArr')),
          resisYearType: localStorage.getItem('ResisYearType'),
          year: '2015',
      };
  },
  methods:{
      toInterface(){
          this.$router.push({ path: '/analyseDrugResis' })
      },
      getMap(){   //地图
          let that = this;

          echarts.registerMap('china',chinaMap);
          let myMap =  echarts.init(document.getElementById('map'));

          let seriesArr = [];

          //下面这个循环是药物，如AMP、AMK
          for(let i = 0; i < that.drugAnalyseArr.length; i++) {
            let seriesArrObj = {
              type: 'map',
              mapType: 'china',
              label: {
                  normal: {
                      show: true
                  },
                  emphasis: {
                      show: true
                  }
              },
            };
            seriesArrObj.name = that.drugAnalyseArr[i]; // AMP、AMK
            seriesArrObj.data = [];  // 这是mychart的series所需，格式如：data:[ {name: '北京', value: 0.2 } ],
            for( let j = 0; j < that.chartSet.length; j ++ ){ // 第一层循环获得所有省份，广东、安徽...
              const province =  that.chartSet[j].type; // 这个就是省份了，广东、安徽...
              for( let k = 0; k < (that.chartSet[j].chart).length; k ++ ) { // 这个是第二层循环，年份来的，比对用户下拉选择的年份
                for( let m = 0; m < ((that.chartSet[j].chart)[k]).length; m ++ ) {
                  if( ((that.chartSet[j].chart)[k])[m].yearStype ==  that.year && ((that.chartSet[j].chart)[k])[m].drug == that.drugAnalyseArr[i] ) {
                    seriesArrObj.data.push({
                      name: that.chartSet[j].type, //这个就是省份了，广东、安徽...
                      value: ((that.chartSet[j].chart)[k])[m].Part
                    })
                  }
                }
              }

            }
            seriesArr.push(seriesArrObj);
          }

          myMap.setOption({
              title: {
                  text: '数据库耐药性分析',
                  // subtext: '按省份统计',
                  left: 'center'
              },
              tooltip: {
                  trigger: 'item'
              },
              legend: {
                  orient: 'vertical',
                  left: 'left',
                  selectedMode: 'single',
                  data: that.drugAnalyseArr
              },
              visualMap: {
                  min: 0,
                  max: 1,
                  precision: 2,
                  left: 'left',
                  top: 'bottom',
                  text: ['高','低'],           // 文本，默认为数值文本
                  calculable: true
              },
              toolbox: {
                  show: true,
                  orient: 'vertical',
                  left: 'right',
                  top: 'center',
                  feature: {
                      saveAsImage: {}
                  }
              },
              series: seriesArr
          })
      },
      getColumn(){  //柱状图
          let that = this
          if(that.chartSet && that.chartSet.length > 0){
              for( var i = 0; i < that.chartSet.length; i++){   //统计类型的每一个，如一个循环：猪
              var html_id = "chart" + i;

              //y轴——时间单位
              var _timeType = [];
              for( var j = 0; j < (that.chartSet[i].chart)[0].length; j++ ){
                  _timeType.push(  ((that.chartSet[i].chart)[0])[j].yearStype   );
              }

              //具体数值
              var dataSet = [];
              for( var j = 0; j < (that.chartSet[i].chart).length; j++){   //每个循环，一个菌属
                  var dataArr = [];
                  var numberArr = [];
                  var totalArr = [];
                  for( var k = 0; k < (that.chartSet[i].chart)[j].length; k++){
                      dataArr.push( ( ( that.chartSet[i].chart)[j])[k].Part );
                      numberArr.push( ( ( that.chartSet[i].chart)[j])[k].Number );
                      totalArr.push( ( ( that.chartSet[i].chart)[j])[k].Total );
                  }
                  dataSet.push({
                        // name: ((that.chartSet[i].chart)[j])[0].drug,
                        name: ((that.chartSet[i].chart)[j])[0] ? ((that.chartSet[i].chart)[j])[0].drug : "",
                        type: 'bar',
                        barMaxWidth : 13,
                        data: dataArr,
                        number: numberArr,
                        total: totalArr,
                  })
              }

              var myChart = echarts.init(document.getElementById(html_id));
              // 绘制图表
              myChart.setOption({
                  title: {
                      text: '耐药性分析图表',
                      subtext: '按' + that.typeAnalyse + '【' + (that.chartSet[i].type == undefined ? "" : that.chartSet[i].type) +'】统计, 药物为【' + that.drugAnalyse + '】'
                  },
                  legend: {
                      data: that.drugAnalyseArr
                  },
                  color:  that.colorChart,

                  grid: {
                        left: '3%',
                        right: '3%',
                        bottom: '3%',
                        containLabel: true
                  },
                  tooltip: {
                        trigger: 'axis',
                        axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                        }
                  },
                  toolbox: {
                        show : true,
                        feature : {
                            dataView : {
                                show: true,
                                readOnly: false,
                                optionToContent: function(opt) {
                                    var axisData = opt.xAxis[0].data;
                                    var series = opt.series;

                                    var table = '<div style="width: 100%;height: 370px; overflow-y:scroll;"><table style="width:98%;text-align:center;"><thead><tr>'
                                                 + '<th>时间</th>';

                                    for( var i = 0; i < series.length; i++ ){
                                         table = table + '<th>' + series[i].name + '</th>' + '<th>总菌数</th>' + '<th>耐药菌数</th>';
                                    }
                                    table = table + '</tr></thead><tbody>';

                                    for( var i = 0; i < axisData.length;  i++ ){    //左边表头，如时间
                                        table = table + '<tr>' + '<td>' + axisData[i] + '</td>';
                                        for( var j = 0; j < series.length; j++ ){
                                                table = table + '<td>' +  series[j].data[i] +'</td>' + '<td>' +  series[j].total[i] +'</td>' + '<td>' +  series[j].number[i] +'</td>';
                                        }
                                        table += '</tr>';
                                    }

                                    table += '</tbody></table></div>';
                                    return table;
                                }
                            },
                            magicType : {show: true, type: ['line', 'bar']},
                            restore : {show: true},
                            saveAsImage : {show: true}
                        }
                  },
                  xAxis: {

                      data: _timeType
                  },
                  yAxis: {
                      type : 'value',
                      boundaryGap: false,
                      axisLabel: {
                        formatter: function(value, index) {
                          let str = (value * 100).toFixed(0).toString() + '%';
                          return str;
                        }
                      }
                    //   data : [0, 0.25, 0.5, 0.75, 1, 1.25, 1.5]
                  },
                  series: dataSet
              });
          }
          }

      },
  },
  created(){

  },
  mounted(){
      this.getColumn();
      if(this.resisYearType == "年"){
        this.year = '2015';
        this.getMap();
      }
  },
  watch:{
      'color':function(){
          if(this.color!=""){
              this.colorChart = this.color
          }else{
              this.colorChart = ['#7cb5ec', '#f7a35c', '#8085e9', '#f15c80', '#e4d354', '#2b908f', '#f45b5b', '#91e8e1', '#434348']
          }
          this.getColumn()

      },
      'year': function (val) {
        this.getMap();
      }
  }
}

</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
.operatBtnG{
    padding-left: 50px;
}
.chartDIV{
    margin-top: 30px;
}
.chartCanvas{
    margin:  100px 0;
}
.colorSpan{
    float: left;
    display:inline-block;
    width:20px;
    height:20px;
    border-radius: 50%;
}
/*.chartCanvas div div div{
	height: 1000px !important;
	overflow: scroll;
}*/
</style>



