<!-- 基因亚型管理 | created by JanvierCheng -->
<template>
  <div id="sourceNumberDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav">
              <my-location title="基因亚型管理"></my-location>
              <my-navigat activ="3-6"></my-navigat>
          </div>
          <div class="ho-cotet">
              <div>
                  <el-button-group>
                    <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                    <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                    <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height="600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_1_SubType"
                      label="第一种亚型">
                    </el-table-column>
                    <el-table-column
                      prop="TB_2_SubType"
                      label="第二种亚型">
                    </el-table-column>
                    <el-table-column
                      prop="TB_3_SubType"
                      label="第三种亚型">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      label="备注">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[10,20,30,40]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
                <div>
                  <span>{{currentMount}}</span>
                </div>
              </div>
              <el-dialog title="基因管理" v-model="dialogFormVisible" >
                  <el-form :model="formAdd"  ref="formAdd">
                    <el-form-item label="第一种亚型" :label-width="formLabelWidth" prop="subType1">
                      <el-input v-model="formAdd.subType1" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="第二种亚型" :label-width="formLabelWidth" prop="subType2">
                      <el-input v-model="formAdd.subType2" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="第三种亚型" :label-width="formLabelWidth" prop="subType3">
                      <el-input v-model="formAdd.subType3" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>

  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import config from './../publicAPI/config'
import axios from 'axios'

export default {
  // name: 'login',
  components: {
      myHeader,
      myLocation,
      myNavigat
  },
  data(){
      return {
          URL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          dialogFormVisible: false,
          currentOpert: true,
          formAdd: {
              subType1: '',
              subType2: '',
              subType3: '',
              remark: '',
          },
          // formAddRules:{
          //     sourceno: [
          //         { required: true, message: '请输入来源号', trigger: 'blur' },
          //         { min: 1, max: 11, message: '长度在 1 到 11 个字符', trigger: 'blur' }
          //     ],
          //     sourcename: [
          //         { required: true, message: '请输入名称', trigger: 'blur' },
          //     ]
          // },
          loading2: false,
          currentPage: 1,
          currentMount: '',
          pageSize: 10,
          totalPage: 0,
          formLabelWidth: '100px'
      }
  },
  methods:{
      onAdd(){
          this.dialogFormVisible = true
          this.currentOpert = true
      },
      onEdit(){
          if(this.multipleSelection.length==1){
              this.dialogFormVisible = true
              this.currentOpert = false
              this.formAdd = {
                  id: this.multipleSelection[0].TB_id,
                  subType1: this.multipleSelection[0].TB_1_SubType,
                  subType2: this.multipleSelection[0].TB_2_SubType,
                  subType3: this.multipleSelection[0].TB_3_SubType,
                  remark: this.multipleSelection[0].TB_remark
              }
          }
      },
      onDelete(){
          if(this.multipleSelection.length>0){
              let that = this;
              this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                  let str = "";
                  for(var item of this.multipleSelection){
                      str = str + item.TB_id + ','
                  }
                  axios.post(that.URL+'/genotypingDir/deletion', {
                     ids: str
                  })
                  .then(function (res) {
                      if(res.data.state){
                          that.$message({
                            type: 'success',
                            message: res.data.value
                          });
                          that.getList();
                      }else{
                          that.$message({
                            type: 'error',
                            message: res.data.value
                          });
                      }
                  })
                  .catch(function (error) {
                  });

              }).catch((error) => {
                  // this.$message.error(error);
              });
          }
      },
      onConfirm(){
          let that = this
          let param = {
              genotyping1: this.formAdd.subType1,
              genotyping2: this.formAdd.subType2,
              genotyping3: this.formAdd.subType3,
              remark: this.formAdd.remark,
          }
          this.$refs['formAdd'].validate((valid) => {
            if (valid) {
                if( this.currentOpert){//新增
                    axios.post(that.URL+'/genotypingDir/addition', param)
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.dialogFormVisible = false
                            that.getList()
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                      console.error(error);
                    });
                    this.dialogFormVisible = false
                }else{//修改
                    param.id = this.formAdd.id;
                    axios.post(that.URL+'/genotypingDir/update', param)
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.dialogFormVisible = false
                            that.getList()
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                      console.error(error);
                    });
                    this.dialogFormVisible = false
                }
            } else {
              console.log('error submit!!');
              return false;
            }
        });

      },
      getList(){
          let that = this;
          var param = {
              page: that.currentPage,
              rows: that.pageSize,
          }
          axios.post(that.URL+'/genotypingDir/list', param)
          .then(function (res) {
              if( res.data.state) {
                that.tableData = [];
                that.totalPage = res.data.value.pages;
                that.currentMount =
                  `本页显示: ${(res.data.value.currentPage - 1) * res.data.value.rows + 1}-${res.data.value.currentPage === res.data.value.pages ? res.data.value.total
                  : res.data.value.currentPage * res.data.value.rows}，共有${res.data.value.total}条数据`;
                for( var item of res.data.value.data){
                    that.tableData.push({
                        TB_id: item.id,
                        TB_1_SubType: item.genotyping1,
                        TB_2_SubType: item.genotyping2,
                        TB_3_SubType: item.genotyping3,
                        TB_remark: item.remark
                    })
                }
                that.loading2 = false;
              } else {
                that.$message.error(res.data.value);
              }

          })
          .catch(function (error) {
            console.error(error);
          });
      },
      resetForm(formName){
          this.$refs[formName].resetFields();
          if(formName == 'formSamInfo'){
              this.getList();
          }else if(formName == 'formAdd'){
              this.formAdd =  {
                  subType1: '',
                  subType2: '',
                  subType3: '',
                  remark: ''
              }
              this.dialogFormVisible = false
          }
      },
      handleSelectionChange(val) {
          this.multipleSelection = val;
        //   console.log(this.multipleSelection);
      },
      handleSizeChange(val) {
          this.pageSize = val;
          this.getList();
          console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
          this.currentPage = val;
          this.getList();
          console.log(`当前页: ${val}`);
      }
  },
  created(){
      this.getList();
  },
  watch:{
    //   'dialogFormVisible':function(){
    //       if(!this.dialogFormVisible){
    //           this.$refs['formAdd'].resetFields();
    //       }
    //   }
  }

}
</script>

<style scoped>
.el-button-group{
    margin-bottom: 30px;
}
.inputGp{
    margin-bottom: 20px;
}
.inputGp .el-input{
    width: 240px;
}
.el-form-item{
    margin-bottom: 0 !important;
}
.block{
    margin-top: 30px;
}
.el-dialog .el-form-item{
    margin-bottom: 20px !important;
}
</style>



