<!-- 基因检出率分析 | created by JanvierCheng -->
<template>
  <div id="sampleSiteInfoDiv">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="基因检出率分析"></my-location>
              <my-navigat activ="2-1"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="70px" :inline="true" class="demo-form-inline">
                      <el-form-item label="菌株编号" prop="strainno">
                          <el-input v-model="formSamInfo.strainno"></el-input>
                      </el-form-item>
                      <el-form-item label="样品编号" prop="sampleno">
                          <el-input v-model="formSamInfo.sampleno"></el-input>
                      </el-form-item>
                      <el-form-item label="内部编号" prop="inno">
                          <el-input v-model="formSamInfo.inno"></el-input>
                      </el-form-item>
                      <el-form-item label="采样地名" prop="caiyangdi">
                          <el-input v-model="formSamInfo.caiyangdi"></el-input>
                      </el-form-item>
                      <el-form-item label="基因名称" prop="genename">
                          <el-input v-model="formSamInfo.genename"></el-input>
                      </el-form-item>
                      <el-form-item label="备注" prop="remark">
                          <el-input v-model="formSamInfo.remark"></el-input>
                      </el-form-item>
                      <el-form-item label="省份" prop="province">
                          <el-select  v-model="formSamInfo.province" multiple placeholder="选择省份">
                              <el-option :value="prvnc.name" v-for="prvnc in prvncs"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="来源" prop="source">
                          <el-select  v-model="formSamInfo.source" multiple placeholder="选择来源">
                              <el-option :value="item.sourcename" v-for="item in sourceSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="动物" prop="animal">
                          <el-select  v-model="formSamInfo.animal" multiple placeholder="选择动物">
                              <el-option :value="item.animalname" v-for="item in animalSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="一级亚型" prop="subType1">
                          <el-select  v-model="formSamInfo.subType1" placeholder="选择一级亚型" :disabled="disabled1">
                              <el-option :value="item" v-for="item in subType1"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="二级亚型" prop="subType2">
                          <el-select  v-model="formSamInfo.subType2" placeholder="选择二级亚型" :disabled="disabled2">
                              <el-option :value="item" v-for="item in subType2"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="三级亚型" prop="subType3">
                          <el-select  v-model="formSamInfo.subType3" multiple placeholder="选择三级亚型" :disabled="disabled3">
                              <el-option :value="item" v-for="item in subType3"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="采样时间">
                          <el-date-picker type="date" placeholder="选择日期" v-model="formSamInfo.date1" :editable="false" style="width: 144px !important;"></el-date-picker>
                          -&nbsp;<el-date-picker type="date" placeholder="选择日期" v-model="formSamInfo.date2" :editable="false" style="width: 144px !important;"></el-date-picker>
                      </el-form-item>
                  </el-form>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button type="primary" icon="d-caret" @click="onExport">导出数据</el-button>
                    <el-button type="primary" icon="more" @click="toAnalyse">分析界面</el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button icon="search" @click="getList">搜索</el-button>
                    <el-button icon="close" @click="resetForm('formSamInfo')">重置</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height="600"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35" >
                    </el-table-column>
                    <el-table-column
                      prop="TB_strainno"
                      label="菌株编号"
                      fixed
                      width="120">
                    </el-table-column>
                    <el-table-column
                      prop="TB_inno"
                      label="内部编号"
                      fixed
                      width="106">
                    </el-table-column>
                    <el-table-column
                      prop="TB_sampleno"
                      label="样品编号"
                      fixed
                      width="106">
                    </el-table-column>
                    <el-table-column
                      prop="TB_geneName"
                      label="基因名称"
                      width="106">
                    </el-table-column>
                    <el-table-column
                      prop="TB_caiyangtime"
                      label="采样时间"
                      width="120">
                    </el-table-column>
                    <el-table-column
                      prop="TB_province"
                      label="省份"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_caiyangdi"
                      label="采样地"
                      width="240">
                    </el-table-column>
                    <el-table-column
                      prop="TB_animal"
                      label="动物"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_subtype1"
                      label="一级亚型"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_subtype2"
                      label="二级亚型"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_subtype3"
                      label="三级亚型"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_source"
                      label="来源"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      label="备注"
                      width="100">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[50, 100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
                <div>
                  <span>{{currentMount}}</span>
                </div>
              </div>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import dataSet from './../publicAPI/dataSet'
import axios from 'axios'
import dataRules from './../publicAPI/dataRules'
import config from './../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data(){
      return{
          URL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          formSamInfo: {
              inno: "",
              sampleno: "",
              strainno: "",
              province: [],
              caiyangdi: "",
              genename: "",
              animal: [],
              source: [],
              date1: "",
              date2: "",
              subType1: "",
              subType2: "",
              subType3: [],
              remark: ""
          },
          switchSH: false,   //查询
          switchNV: true,   //导航
          cotDivWidth:{
              width: '1150px',
          },
          totalPage: 0, //总页数
          pageSize: 50, //每页记录数
          dialogFormVisible: false,
          formLabelWidth: '85px',
          currentPage: 1,   //当前页码
          currentMount: '',
          currentOper: true,  //添加是true,修改是false
          prvncs: dataSet.province,
          partSelect: [],
          animalSelect: [],
          sourceSelect: [],
          subType1: [],
          disabled1: false,
          subType2: [],
          disabled2: true,
          subType3: [],
          disabled3: true,
      }
  },
  methods:{
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        toAnalyse(){
            let that = this
            this.formSamInfo.date1 = dataRules.formatDate(that.formSamInfo.date1);
            this.formSamInfo.date2 = dataRules.formatDate(that.formSamInfo.date2);
            localStorage.setItem('geneJianchuRate', JSON.stringify(that.formSamInfo));
            this.$router.push({path: '/analyseGeneResis'})
        },
        getSelect(){
            let that = this
            axios.all([axios.get(that.URL + '/animal/selection'),
                       axios.get(that.URL + '/source/selection'),
                       axios.get(that.URL + '/part/selection')])
                .then(axios.spread((animalres, sourceres, partres) => {
                    if(animalres.data.state){
                        that.animalSelect = animalres.data.value
                    }
                    if(sourceres.data.state){
                        that.sourceSelect = sourceres.data.value
                    }
                    for(var item of partres.data.value){
                        if(item.partname == ""){
                            that.partSelect.push({
                                partno: item.partno,
                                partname: " "
                            })
                        }else{
                            that.partSelect.push({
                                partno: item.partno,
                                partname: item.partname
                            })
                        }
                    }
                }))
        },
        getList(){
            let that = this
            var param = {
                strainno: that.formSamInfo.strainno,
                genalias: that.formSamInfo.inno,
                sampleno: that.formSamInfo.sampleno,
                farmName: that.formSamInfo.caiyangdi,
                genname: that.formSamInfo.genename,
                genremarks: that.formSamInfo.remark,
                province: that.formSamInfo.province,    //[省份]
                sampleSource: that.formSamInfo.source,    //[来源]
                animalname: that.formSamInfo.animal, //[动物 - 编号]
                genotyping1: that.formSamInfo.subType1.split(""),
                genotyping2: that.formSamInfo.subType2.split(""),
                genotyping3: that.formSamInfo.subType3,
                beginDate: dataRules.formatDate(that.formSamInfo.date1),
                endDate: dataRules.formatDate(that.formSamInfo.date2),
                page: that.currentPage,
                rows: that.pageSize,
            }
            // console.warn(JSON.stringify(param))
            axios.post( that.URL + '/genanalysis/list', param )
                .then((res) => {
                    that.tableData = [];
                    that.totalPage = res.data.value.pages;
                    that.currentMount =
                      `本页显示: ${(res.data.value.currentPage - 1) * res.data.value.rows + 1}-${res.data.value.currentPage === res.data.value.pages ? res.data.value.total
                      : res.data.value.currentPage * res.data.value.rows}，共有${res.data.value.total}条数据`;
                    for( var item of res.data.value.data){
                        that.tableData.push({
                            TB_id: item.id,
                            TB_strainno: item.strainno,
                            TB_sampleno: item.sampleno,
                            TB_geneName: item.genname,
                            TB_inno: item.genalias,
                            TB_caiyangtime: item.sampledate,
                            TB_province: item.sampleProvince,
                            TB_caiyangdi: item.farmName,
                            TB_yangzhi: item.samplefarmaddr,
                            TB_animal: item.animalname,
                            TB_animal_id: item.animalno,
                            TB_subtype1: item.genotyping1,
                            TB_subtype2: item.genotyping2,
                            TB_subtype3: item.genotyping3,
                            TB_source: item.sampleSource,
                            TB_remark: item.genremarks,
                        })
                    }
                })
                .catch((error) => {
                    console.error(error);
                })
        },
        getSelect1(){
          let that = this
          axios.get(that.URL + '/genotypingDir/selection/genotyping1')
            .then((res) => {
              if(res.data.state) {
                that.subType1 = [];
                for( let item of res.data.value ) {
                  that.subType1.push(item);
                }
              } else {
                that.$message.error(res.data.value);
              }
            })
            .catch((err) => {
              console.error(err);
            });
        },
        getSelect2(){
          let that = this
          axios.get(that.URL + '/genotypingDir/selection/genotyping2?genotyping1=' + that.formSamInfo.subType1)
            .then((res) => {
              if(res.data.state) {
                that.subType2 = [];
                for( let item of res.data.value ) {
                  that.subType2.push(item);
                }
              } else {
                that.$message.error(res.data.value);
              }
            })
            .catch((err) => {
              console.error(err);
            });
        },
        getSelect3(){
          let that = this
          axios.get(that.URL + '/genotypingDir/selection/genotyping3?genotyping2=' + that.formSamInfo.subType2)
            .then((res) => {
              if(res.data.state) {
                that.subType3 = [];
                for( let item of res.data.value ) {
                  that.subType3.push(item);
                }
              } else {
                that.$message.error(res.data.value);
              }
            })
            .catch((err) => {
              console.error(err);
            });
        },
        resetForm(formName) {
            // this.$refs[formName].resetFields();
            this.formSamInfo = {
                inno: "",
                sampleno: "",
                strainno: "",
                province: [],
                caiyangdi: "",
                genename: "",
                animal: [],
                source: [],
                date1: "",
                date2: "",
                subType1: "",
                subType2: "",
                subType3: [],
                remark: ""
            };
            this.disabled2 = true;
            this.disabled3 = true;
            this.getList();
        },
        onExport(){
          let that = this;
          if((this.multipleSelection).length > 0){
              var real_id = '';
              for ( var item of this.multipleSelection){
                  real_id = real_id + item.TB_id + ','
              }
              axios.get(that.URL + '/genanalysis/download?id=' + real_id)
                  .then((res) => {
                      window.open(that.URL + '/genanalysis/download?id=' + real_id)
                  })
                  .catch((error) =>{
                      console.error(error);
                  })
          }else{
              this.$message.error('请选择要导出的行！');
          }
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
            console.log(`当前页: ${val}`);
        }
  },
  watch:{
      switchNV(val){
          if(val){
             this.cotDivWidth = {
                  width: '1150px',
             }
         }else{
             this.cotDivWidth = {
                  width: '100%',
             }
         }
     },
     'formSamInfo.subType1':function(val){
       if(!!val){
         this.disabled3 = true;
         this.disabled2 = false;
       }
       this.formSamInfo.subType2 = "";
       this.formSamInfo.subType3 = [];
       this.getSelect2();
     },
     'formSamInfo.subType2':function(val){
       if(!!val){
         this.disabled3 = false;
       }
       this.formSamInfo.subType3 = [];
       this.getSelect3();
     },
  },
  created(){
      this.getSelect();
      this.getList();
      this.getSelect1();
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
/*表单输入组*/
.inputGp{
    margin-bottom: 20px;
}
.inputGp .el-form-item{
    margin-bottom: 6px;
}
.inputGp .el-input, .inputGp .el-select{
    width: 260px;
    margin: 0;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
.el-dialog .el-form-item{
    margin-bottom: 20px !important;
}
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



